<template>
  <div class="horionzal-content" v-if="couponList.length">
    <div class="horionzal-row">
      <div class="coupon-item" v-for="(item,index) in couponList.slice(0, 3)" :key="item.couponTemplateCode" @click="doGetCoupon(item,index)">
        <div class="coupon-info">
          <coupon-value-label
            :fontSize="'.42rem'"
            :forHomePage="true"
            :couponValue='item.couponValue'
            :couponType='item.couponType'
          />
          <div class="coupon-text">
            {{item.description}}
          </div>
        </div>
        <div class="coupon-btn">
          {{item.displayType == '2' ? '去 使 用' : '领 取'}}
        </div>
        <div class="icon-getted" v-if="item.displayType == '2'"/>
      </div>
      <div class="coupon-more" v-if="couponList.length > 3" @click="more()"/>
    </div>
  </div>
</template>
<script>
import { receiveCoupon, getCouponsCenter } from '@/http/service/shop';
import couponValueLabel from '@/components/couponValueLabel'
/** 精选--优惠券列表 */
export default {
  props: {
    data: {
      type: Object,
      default: () => {},
    },
    storeId: {
      type: String,
      default: ''
    },
  },
  components: {
    couponValueLabel,
  },
  data() {
    return {
      couponList: [],
    }
  },
  watch: {
    storeId() {
      this.getCouponList();
    }
  },
  mounted() {
    this.getCouponList();
  },
  methods: {
    getCouponList() {
      const params = {
        storeId: this.storeId,
        couponCateId: '',
        startIndex: 1,
        pageSize: 20,
      };
      getCouponsCenter(params).then(res => {
        if (res.success) {
          this.couponList = res.data;
        } else {
          this.$toast(res.message || '获取数据失败，请稍后重试！');
        }
      });
    },
    doGetCoupon(coupon, index) {
      console.log('coupon', coupon)
      const { activityCode, couponTemplateCode, displayType, couponType } = coupon;
      const storeId = this.storeId
      // 25093埋点
      this.$util.gioTrackWithVariable('mall_store_click_model', {
        modelName: '优惠券列表',
        modelCode: 'coupon',
        site_number: index + 1 + '',
        itemCode: couponTemplateCode + '',
      })
      if (Number(displayType) === 2) {
        this.$util.gioTrackWithVariable('MB15618', {
          ad_coupon_id: coupon.couponTemplateCode ? coupon.couponTemplateCode + '' : 'null'
        })
        this.$router.push(`/usablecouponlist?activityCode=${activityCode}&couponTemplateCode=${couponTemplateCode}&couponType=${couponType}&storeId=${storeId}`)
      } else {
        this.$util.gioTrackWithVariable('MB15617', {
          ad_coupon_id: coupon.couponTemplateCode ? coupon.couponTemplateCode + '' : 'null'
        })
        if (this.$store.state.isLogin) {
          receiveCoupon({
            activityCode,
            couponTemplateCode,
            streetId: this.$store.getters['getlocationInfo'].townshipId,
          }).then(res => {
            if (res.success) {
              // 如果可以领取的优惠券数量为0，要手动将优惠券的的状态改成 去使用
              let _list = this.couponList;// 优惠券列表list
              _list.forEach(item => {
                if (item.activityCode === activityCode && item.couponTemplateCode === couponTemplateCode) {
                  item.displayType = '2';
                }
              })
            } else {
              this.$toast(res.message)
            }
          })
        } else {
          // this.$router.push('/login')
          this.$store.dispatch('doLogin')
        }
      }
    },
    more() {
      // 25093埋点
      this.$util.gioTrackWithVariable('mall_store_click_model', {
        modelName: '优惠券列表',
        modelCode: 'coupon',
        site_number: 'null',
        itemCode: '更多',
      })
      this.$router.push(`/voucher?storeCouponid=${this.storeId}`);
    }
  },
}
</script>
